.cloud_navigation {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    -ms-flex-align: stretch;
        align-items: stretch;
    padding: .5rem;
}

.cloud_navigation[aria-expanded=false] {
    width: 40%;
}

.cloud_navigation_dropzone {
    z-index: 0;
    display: contents;
}

@media screen and (max-width:48rem) {
    .cloud_navigation[aria-expanded=false] {
        display: none;
    }
}


